.card{width:100%;max-width:100%;margin:2rem 0;display:flex;flex-direction:column;align-items:center;padding-inline:1rem}.videoWrapper{position:relative;width:100%;max-width:960px;height:540px;overflow:hidden;border-radius:1.5rem;background:#000}.video{position:absolute;top:50%;left:50%;height:100%;width:auto;transform:translate(-50%, -50%);object-fit:cover}.overlay{position:absolute;inset:0;background:#000;opacity:0;transition:opacity .5s ease;pointer-events:none;z-index:1}.logo{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);max-width:400px;max-height:200px;opacity:0;pointer-events:none;transition:opacity .5s ease;z-index:2}.textContent{margin-top:1.5rem;display:flex;justify-content:space-between;align-items:flex-start;gap:2rem;max-width:960px;width:100%;text-align:left;flex-wrap:wrap}.title{font-family:var(--font-inter);font-size:1.5rem;font-weight:600;color:#fff;flex:1 1 30%;min-width:200px}.description{font-family:var(--font-inter);font-size:.95rem;line-height:1.6;color:#ccc;flex:1 1 65%;min-width:250px}